.container {
  font-family: Arial, sans-serif;
  padding: 20px;
  max-width: 800px;
  margin: auto;
  transition: background 0.3s, color 0.3s;
}


.theme-toggle {
  padding: 8px 15px;
  background: #333;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}



.sticky-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: white;
  padding: 10px 20px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
}


h1 {
  margin-top: 1em;
  text-align: center;
  font-weight: bold;
  font-size: 2em;
}


h2 {
  text-align: center;
  font-weight: bold;
  font-size: 1em;
  margin-top: 1em;
  margin-bottom: 1em;
}

.search-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: white;
  padding: 10px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 1000;
}

.search-bar {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.type-header {
  margin-top: 60px;
  font-size: 20px;
  font-weight: bold;
}

.card-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 10px;
}

